/* font-family: 'Architects Daughter', cursive;
font-family: 'Roboto', sans-serif; */
.body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  font-size: 18px;
  line-height: 1.6;
  color: #e4e4e4;
}

*,
*:before,
*:after {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5 {
  margin: 0;
}

/*container*/
.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
}
